/**
 * List of colour variables for the different parts of the minimap
 */
.kui-theme--light .pipeline-minimap-node rect {
  fill: #f0f1f3; }

.kui-theme--dark .pipeline-minimap-node rect {
  fill: #2a3b46; }

.kui-theme--light .pipeline-minimap-node--active rect {
  fill: #fff; }

.kui-theme--dark .pipeline-minimap-node--active rect {
  fill: #293b46; }

.kui-theme--light .pipeline-minimap-node--selected rect {
  fill: #81f4ff; }

.kui-theme--dark .pipeline-minimap-node--selected rect {
  fill: #00b0f5; }

.kui-theme--light .pipeline-minimap-node--faded rect {
  fill: #c3c8ce; }

.kui-theme--dark .pipeline-minimap-node--faded rect {
  fill: #0e1b24; }

.pipeline-minimap__graph {
  display: block; }

.pipeline-minimap-container {
  /* Sub-pixel adjustment */
  position: absolute;
  top: calc(100% + 2px);
  left: 100%;
  width: 500px;
  height: 222px;
  transform: translate(-100%, -100%);
  transition: transform 300ms ease; }
  .kui-theme--light .pipeline-minimap-container {
    background: #a6aeb6;
    box-shadow: 0 0 2px rgba(255, 255, 255, 0.5); }
  .kui-theme--dark .pipeline-minimap-container {
    background: #051116;
    box-shadow: 0 0 2px rgba(255, 255, 255, 0.05); }

.pipeline-minimap {
  position: absolute;
  top: 0;
  right: 0; }

.pipeline-minimap__viewport {
  cursor: move;
  fill: rgba(0, 0, 0, 0);
  /* Solid for pointer events */
  stroke-width: 1px; }
  .kui-theme--light .pipeline-minimap__viewport {
    stroke: #d3dee3; }
  .kui-theme--dark .pipeline-minimap__viewport {
    stroke: #8e8e90; }
