.pipeline-minimap-toolbar {
  display: block;
  margin-top: auto;
  padding: 0;
  list-style: none; }

.pipeline-minimap-button {
  height: 46px;
  transition: background-color 300ms ease; }

.kui-theme--light .pipeline-minimap-button.pipeline-icon-toolbar__button--active {
  background: #f0f1f3; }

.kui-theme--dark .pipeline-minimap-button.pipeline-icon-toolbar__button--active {
  background: #1d303b; }

.pipeline-minimap-button--map {
  padding-top: 4px; }

.pipeline-minimap-toolbar__scale {
  display: block;
  width: 100%;
  padding: 6px 8px 6px;
  font-weight: bold;
  font-size: 10px;
  text-align: center; }
  .kui-theme--light .pipeline-minimap-toolbar__scale {
    color: rgba(0, 0, 0, 0.85); }
  .kui-theme--dark .pipeline-minimap-toolbar__scale {
    color: rgba(255, 255, 255, 0.85); }
