.pipeline-sidebar {
  position: absolute;
  top: -1px;
  /* Avoids pixel rounding gaps */
  bottom: -1px;
  left: 0;
  z-index: 1;
  display: flex;
  width: 100%;
  transform: translateX(calc(-100% + 60px));
  transition: transform ease 0.4s; }
  @media (min-width: 700px) {
    .pipeline-sidebar {
      width: 400px;
      transform: translateX(-340px); } }
  @media print {
    .pipeline-sidebar {
      display: none; } }
  .pipeline-sidebar--visible {
    transform: translateX(0); }
  .kui-theme--light .pipeline-sidebar {
    border-bottom: 1px solid rgba(0, 0, 0, 0.05);
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.07); }
  .kui-theme--dark .pipeline-sidebar {
    border-bottom: 1px solid rgba(0, 0, 0, 0.2);
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.3); }

.pipeline-ui {
  position: relative;
  z-index: 1;
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  height: 100%;
  padding-top: 1.8em;
  visibility: hidden;
  transition: visibility 0.3s; }
  .pipeline-sidebar--visible .pipeline-ui {
    visibility: visible;
    transition: visibility 0s; }
  .kui-theme--light .pipeline-ui {
    background: #f0f1f3; }
  .kui-theme--dark .pipeline-ui {
    background: #1d303b; }

.pipeline-toolbar {
  position: relative;
  z-index: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 60px;
  height: 100%; }
  .kui-theme--light .pipeline-toolbar {
    background: #e6e8eb; }
  .kui-theme--dark .pipeline-toolbar {
    background: #122531; }
