/**
 * List of colour variables for the different parts of the flowchart
 */
.pipeline-node text {
  font-weight: 600;
  font-size: 1.4em;
  transition: fill ease 0.1s, text-shadow ease 0.1s, opacity ease 0.1s;
  pointer-events: none; }
  .kui-theme--light .pipeline-node text {
    fill: rgba(0, 0, 0, 0.85); }
  .kui-theme--dark .pipeline-node text {
    text-shadow: 0 0 6px black;
    fill: rgba(255, 255, 255, 0.85); }

.pipeline-node rect {
  transition: fill ease 0.1s, stroke ease 0.1s, opacity ease 0.1s;
  stroke-width: 1px; }
  .kui-theme--light .pipeline-node rect {
    fill: #f0f1f3;
    stroke: #c6cace; }
    @media print {
      .kui-theme--light .pipeline-node rect {
        fill: #f0f1f3;
        stroke: #6b6a6b; } }
  .pipeline-graph--export.kui-theme--light .pipeline-node rect {
    fill: #f0f1f3;
    stroke: #6b6a6b; }
  .kui-theme--dark .pipeline-node rect {
    fill: #2a3b46;
    stroke: #354854; }

.pipeline-node:focus {
  outline: none; }
  [data-whatinput='keyboard'] .pipeline-node:focus rect {
    stroke: #00b0f5;
    stroke-width: 3.5px; }

.kui-theme--light .pipeline-node--active rect {
  fill: #c6e6fb;
  stroke: #b2cada; }

.kui-theme--dark .pipeline-node--active rect {
  fill: #124066;
  stroke: #234f73; }

.pipeline-node--selected rect {
  stroke-width: 2px; }
  .kui-theme--light .pipeline-node--selected rect {
    fill: #aad8ff;
    stroke: #00b0f5; }
  .kui-theme--dark .pipeline-node--selected rect {
    fill: #004d8c;
    stroke: #00b0f5; }

.pipeline-node--faded {
  opacity: 0.3; }

.pipeline-node--icon text {
  opacity: 0;
  pointer-events: none; }

.pipeline-node__icon {
  pointer-events: none; }
  .kui-theme--light .pipeline-node__icon {
    fill: rgba(0, 0, 0, 0.85); }
  .kui-theme--dark .pipeline-node__icon {
    fill: rgba(255, 255, 255, 0.85); }

/**
 * List of colour variables for the different parts of the flowchart
 */
.pipeline-edge path {
  pointer-events: none;
  fill: none;
  stroke-width: 1.5px; }
  .kui-theme--light .pipeline-edge path {
    stroke: #888; }
    @media print {
      .kui-theme--light .pipeline-edge path {
        stroke: #686768; } }
  .pipeline-graph--export.kui-theme--light .pipeline-edge path {
    stroke: #686768; }
  .kui-theme--dark .pipeline-edge path {
    stroke: #666; }

.pipeline-edge--faded {
  opacity: 0.1; }

.kui-theme--light .pipeline-flowchart__arrowhead {
  fill: #2a3b46; }
  @media print {
    .kui-theme--light .pipeline-flowchart__arrowhead {
      fill: #686768; } }

.pipeline-graph--export.kui-theme--light .pipeline-flowchart__arrowhead {
  fill: #686768; }

.kui-theme--dark .pipeline-flowchart__arrowhead {
  fill: #cacaca; }

/**
 * List of colour variables for the different parts of the flowchart
 */
.pipeline-layer {
  opacity: 0;
  transition: opacity ease 0.5s; }
  .kui-theme--dark .pipeline-layer {
    fill: #091d29; }
  .kui-theme--light .pipeline-layer {
    fill: #d9dde1; }
  .pipeline-layer:hover {
    opacity: 1; }
  .pipeline-graph--export .pipeline-layer {
    display: none; }
  @media print {
    .pipeline-layer {
      display: none; } }

.pipeline-flowchart__layer-names {
  position: absolute;
  top: 0;
  left: 0;
  width: 130px;
  height: 100%;
  margin: 0;
  padding: 0;
  list-style: none;
  opacity: 0;
  transition: opacity ease 0.7s;
  pointer-events: none; }
  .pipeline-flowchart__layer-names--visible {
    opacity: 1; }
  .kui-theme--dark .pipeline-flowchart__layer-names {
    background: linear-gradient(to right, #001521, rgba(0, 21, 33, 0)); }
  .kui-theme--light .pipeline-flowchart__layer-names {
    background: linear-gradient(to right, #ccd1d6, rgba(204, 209, 214, 0)); }
  @media print {
    .pipeline-flowchart__layer-names {
      display: none; } }

.pipeline-layer-name {
  position: absolute;
  top: -10px;
  display: flex;
  align-items: center;
  height: 20px;
  padding-left: 15px;
  font-weight: bold;
  font-size: 1.6em;
  white-space: nowrap; }

.pipeline-flowchart {
  height: 100%; }

.pipeline-flowchart__graph {
  display: block; }
  .pipeline-flowchart__graph.pipeline-graph--export.kui-theme--dark {
    background: #001521; }
  @media print {
    .pipeline-flowchart__graph {
      width: 100%;
      height: auto; }
      .pipeline-flowchart__graph #zoom-wrapper {
        transform: inherit; } }
