.kedro-pipeline {
  position: relative;
  height: 100%;
  overflow: hidden;
  font-size: 10px; }
  .kedro-pipeline.kui-theme--light {
    color: #333;
    background: #ccd1d6; }
    @media print {
      .kedro-pipeline.kui-theme--light {
        background: none; } }
  .kedro-pipeline.kui-theme--dark {
    color: #fff;
    background: #001521; }

.pipeline-wrapper {
  height: 100%; }

.pipeline-wrapper__loading {
  position: absolute;
  right: 20px;
  bottom: 20px; }
